.accordion__section {
    display: flex;
    flex-direction: column;
    margin-bottom: 5px;
  }
  
  /* Style the buttons that are used to open and close the accordion panel */
  .accordion {
    background-color: #f5dcd5;
    color: #222;
    cursor: pointer;
    padding: 10px;
    border: none;
    outline: none;
    transition: background-color 0.6s ease;
  }
  
  /* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
  .accordion:hover,
  .active {
    background-color: #f0af9f;
  }
  
  /* Style the accordion content title */
  .accordion__title {
    font-size: 16px;
    text-align: left;
    margin-bottom: 0;
  }
  
  /* Style the accordion chevron icon */
  .accordion__icon {
    margin-left: auto;
    transition: transform 0.6s ease;
  }
  
  /* Style to rotate icon when state is active */
  .rotate {
    transform: rotate(90deg);
  }
  
  /* Style the accordion content panel. Note: hidden by default */
  .accordion__content {
    background-color: white;
    overflow: hidden;
    transition: max-height 0.6s ease;
  }
  
  /* Style the accordion content text */
  .accordion__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    padding: 18px;
  }
.accordionWrapper {
    margin: 0 0 50px 0;
}
.timelineWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.dot {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: #f5baac;
    color: white;
    font-size: 26px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
}

.dashedLine {
    width: 0;
    border: 1px dashed #f5baac;
    min-height: 30px;
    margin-left: 15px;
}

.time {
    display: flex;
    flex-direction: row;
}

.time button {
    margin-left: 10px;
}

.timeContent {
    display: flex;
    flex-direction: column;
    margin: 10px 17px;
    transition: ease-in;
}

.timeContent p {
    font-size: 16px;
    line-height: 24px;
}

.timeContent ul {
    margin: 0 0 16px 0;
}

.timeContent ul li {
    margin-bottom: 8px;
}

@-webkit-keyframes slideIn {
    to {
        height: 100%;
    }
}

@keyframes slideIn {
    to {
        height: 100%;
    }
}
#root {
  box-sizing: border-box;
}

html {
  font-family: 'Roboto', sans-serif;
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
}

p {
  font-size: 22px;
  line-height: 30px;
  font-weight: normal;
  margin-top: 0;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

h2 {
  font-size: 32px;
  line-height: 34px;
  padding: 0;
  margin: 0;
}

h3 {
  font-size: 24px;
  line-height: 32px;
  font-style: italic;
  font-weight: normal;
  margin-top: 0;
}

h4 {
  font-size: 22px;
  line-height: 30px;
  font-weight: normal;
}

h5 {
  font-size: 16px;
  line-height: 24px;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin: 0;
}

.button {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  color: black;
  padding: 0 16px;
  height: 56px;
  border: none;
  border-radius: 6px;
  margin-right: 10px;
  background-color: #f0af9f;
  outline: none;
}

.button:hover {
  background-color: #ea947e;
}

.buttonBlock {
  width: 100%;
  font-size: 18px;
  padding: 10px 15px;
  border: none;
  border-radius: 8px;
  margin-right: 10px;
  background-color: #f5baac;
  outline: none;
}

.buttonLink {
  background-color: white;
  border: none;
  padding: 0;
  margin: 0;
  font-size: 18px;
  color: black;
  outline: none;
}

.buttonLink:hover {
  background-color: none;
}

.siteWrapper {
  padding: 0 150px;
}

.contentWrapper {
  padding: 25px 150px;
}

.centerWrapper {
  display: flex;
  justify-content: center;
}

.background-lightgrey {
  background-color: #f9f9f9;
}

.row {
  display: flex;
  flex-direction: row;
  align-items: center;
  min-height: 400px;
}

.center {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.spaceAround {
  width: 100%;
  justify-content: space-between;
}

.back {
  margin: 5px 10px 0 10px;
}

.back a:hover {
  text-decoration: underline;
}

.textblockWrapper {
