}

@media screen and (max-width: 820px) {
  .navbar-wrapper {
    height: 80px;
    padding: 0 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .navbar-wrapper a {
    color: black;
    list-style-type: none;
    text-decoration: none;
  }

  .navbar-wrapper a:hover {
    text-decoration: underline;
  }

  .contentWrapper {
